body{
  background-color: #F5E6CA;
  color: #343F56;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

*:focus {
  outline: none;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #F54748;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #F5E6CA;
}

.App-link {
  color: #61dafb;
}

.item{
  border: 1px solid #343F56;
  width: 15vw;
  background-color: #FB9300;
  position: relative;
  left: 2.5vw;
  top: 2.5vw;
  justify-content: center;
  border-radius: 1vw;
  overflow: hidden;
  margin-right: 5vw;
  height: min-content;
  padding: 1vw;
  padding-top: 0;
}

.itempic{
  width: 12.5vw;
}

.lisatieto{
  width: 12.5vw;
  position: relative;
  color: #343F56;
  opacity: 75%;
}

.back{
  width: 5vw;
  position: relative;
  left: -45vw;
  top: 1vw;
}

.addtocart {
  background: #FFD599;
  border: 1px solid #FFD599;
  border-radius: 6px;
  box-shadow: rgba(0, 0, 0, 0.1) 1px 2px 4px;
  box-sizing: border-box;
  color: #343F56;
  cursor: pointer;
  display: inline-block;
  font-family: nunito,roboto,proxima-nova,"proxima nova",sans-serif;
  font-size: 16px;
  font-weight: 800;
  line-height: 16px;
  min-height: 40px;
  outline: 0;
  padding: 12px 14px;
  text-align: center;
  text-rendering: geometricprecision;
  text-transform: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  vertical-align: middle;
}

.addtocart:hover,
.addtocart:active {
  font-size: 15px;
}

.addtocart:active {
  opacity: .75;
  background-color: #F5E6CA;
}

.products{
  display: flex;
}

.cart{
  width: 20vw;
  background-color: #FFD599;
  border-radius: 1vw;
  padding: 0.5vw;
  position: relative;
  top: 5.5vw;
  left: 40vw;
}

.total{
  font-weight: bold;
}

.cartitem{
  height: 3vw;
  position: relative;
  display: flex;
  text-align: center;
}

.itemname{
  width: 12.5vw;
  font-size: 1vw;
}

.margin0{
  margin: 0;
}

.itemcount{
  text-align: center;
  font-size: 1vw;
  width: 50px;
  height: 25px;
  padding: 0px;
  border: 1px solid #343F56;
  overflow: hidden;
  background-color: #F5E6CA;
}

.cartbutton{
  width: 27px;
  height: 27px;
  padding: 0px;
  border: 1px solid #343F56;
  font-size: 1vw;
  background-color: #F54748;
  color: #343F56;
}

.checkoutbutton{
  background-color: #F54748;
  border: 1px solid #343F56;
  color: #343F56;
  border-radius: 3px;
  font-size: 1vw;
}

.numofitems{
  width: 7.5vw;
  position: relative;
}

.customerinfo{
  background-color: #FFD599;
  width: 30vw;
  position: relative;
  left: 35vw;
  display: flex;
  border-radius: 1vw;
}

.infoinput{
  background-color: #FFD599;
  border: 0;
  border-bottom: 1px solid #343F56;
  font-size: 1vw;
}

.infoinput:focus{
  border: 0;
  border-bottom: 1px solid #343F56;
}

.personal{
  width: 30vw;
  font-size: 1vw;
}

.delivery{
  width: 30vw;
  font-size: 1vw;
}

.order{
  background-color: #F54748;
  border: 1px solid #343F56;
  border-radius: 3px;
  font-size: 1vw;
  margin: 10px;
  color: #343F56;
}

.back{
  background-color: #F54748;
  border: 1px solid #343F56;
  border-radius: 3px;
  font-size: 1vw;
  color: #343F56;
}

label {
  width: 20% !important;
  display:inline-block;
  text-align: right;
}

.receipt{
  width: 30vw;
  position: relative;
  left: 34.75vw;
  background-color: #FFD599;
  border-radius: 1vw;
  padding: 0.25vw;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus, 
input:-webkit-autofill:active{
    -webkit-box-shadow: 0 0 0 30px #FFD599 inset !important;
            box-shadow: 0 0 0 30px #FFD599 inset !important;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
